import React, { useContext, useEffect, useState } from 'react';
import { Table } from 'reactstrap'
import { WalletContext } from '../../context/Wallet';
import NFTItem from './NFTItem';

function NFTList() {

    const wallet = useContext(WalletContext);
    const { account, library, chainId } = wallet.context;

    const [rows, setRows] = useState([
    ]);

    useEffect(() => {
        updateRows();
    }, [wallet.options])

    const updateRows = async () => {
        const optionItems = await Promise.all(wallet.options.nft.value.map(async (o) => {
            return (
            <NFTItem
             key={o.index+'-'+o.provider}
             id={o.index}
             provider={o.provider}
             tokenId={o.tokenId} />
            )
        }));

        if(optionItems.length == 0)
            optionItems.push(
                <tr key="noOptionsNFT" style={{textAlign:"center", width:'100%'}}>
<th>                    <h6>No options found</h6></th>
                </tr>
            )

        setRows(optionItems)
    }

    return (<Table style={{color: "white", fontSize:'12px'}}>
        <thead >
            <tr>
                <th>#</th>
                <th>Type</th>
                <th>Size</th>
                <th>Strike</th>
                <th>Maturity</th>
                <th>Tokenize</th>
            </tr>
        </thead>
        <tbody>
            {rows}
        </tbody>
    </Table>)
}

export default NFTList;