import React, { useContext, useEffect, useState } from 'react';
import { Table } from 'reactstrap'
import { WalletContext } from '../../context/Wallet';
import OptionItem from './optionItem';

function OptionsList() {

    const wallet = useContext(WalletContext);
    const { account, library, chainId } = wallet.context;

    const [rows, setRows] = useState([
    ]);

    useEffect(() => {
        updateRows();
    }, [wallet.options])

    const updateRows = async () => {
        const optionItems = await Promise.all(wallet.options.hegic.value.map(async (o) => {
            return (
            <OptionItem
             key={o.index+'-'+o.provider}
             id={o.index}
             holder={o.holder}
             amount={o.amount}
             provider={o.provider}
             strike={o.strike}
             expiration={o.expiration} 
             optionType={o.optionType}
             state={o.state} />
            )
        }));

        if(optionItems.length == 0){
            optionItems.push(
                <div key="nooptionsHegic" style={{textAlign:"center", width:'100%'}}>
                    <br></br>
                    <h6>No options found</h6>
                </div>
            )
}
        setRows(optionItems)
    }

    return (<Table style={{color: "white", fontSize:'12px'}}>
        <thead >
            <tr>
                <th>#</th>
                <th>Type</th>
                <th>Size</th>
                <th>Strike</th>
                <th>Maturity</th>
                <th>Tokenize</th>
            </tr>
        </thead>
        <tbody>
            {rows}
        </tbody>
    </Table>)
}

export default OptionsList;